<template lang="html">
  <div class="main-wrapper">
    <gallery></gallery>
    <slide-section :bg="doctor_mn"></slide-section>
    <slide-section :bg="doctor_zh"></slide-section>
    <slide-section :bg="hospital"></slide-section>
    <slide-section :bg="health"></slide-section>
    <aside-component></aside-component>
    <footer-component :m="m"></footer-component>
  </div>
</template>

<script>
import Gallery from '../components/Gallery'
import SlideSection from '../components/SlideSection'
import FooterComponent from '../components/Footer'
import AsideComponent from '../components/Aside'

export default {
  name: 'index_m',
  data(){
    return {
      doctor_mn: 'url(static/m_imgs/my211.png)',
      doctor_zh: 'url(static/m_imgs/my211.png)',
      hospital: 'url(static/m_imgs/yyyy344.png)',
      health: 'url(static/m_imgs/tj213.png)',
      m: true
    }
  },
  components: {
    Gallery,
    SlideSection,
    FooterComponent,
    AsideComponent
  }
}
</script>

<style lang="css" scoped>
  .main-wrapper{
    width: 100%;
    max-width: 980px;
    margin: 0 auto;
    box-sizing: border-box;
  }
</style>



