<template lang="html">
  <section class="house-call">
  <div class="call-slide-wrapper">
    <div class="call-container">
      <div class="container-box">
        <span class="container-box-title">选择预约时间</span>
        <span class="container-box-fee">诊费 20 元</span>
      </div>
      <figcaption>医师排班表</figcaption>

      <swiper :options="swiperOption" ref="mySwiper">
        <swiper-slide>
          <div class="cell-item">
          <table>
            <thead>
              <tr>
                <td><img src="../assets/imgs/date-slash.png" alt="date-choose"></td>
                <td v-for="(week, index) in weeks">
                  <p >{{week}}</p>
                  <p>{{month}} - {{days[index] | leftpad}}</p>
                </td>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>上午</td>
                <td v-for="(day, index) in days" @click="slideUp">
                  <span :class="{green: act(index)}">可约</span>
                </td>
              </tr>
              <tr>
                <td>下午</td>
                <td v-for="(day, index) in days" @click="slideUp">
                  <span :class="{green: act(index+1)}">可约</span>
                </td>
              </tr>
            </tbody>
          </table>
          </div>
        </swiper-slide>

        <swiper-slide>
          <div class="cell-item">
          <table>
            <thead>
              <tr>
                <td><img src="../assets/imgs/date-slash.png" alt="date-choose"></td>
                <td v-for="(week, index) in weeks"><p >{{week}}</p>
                  <p>{{month}} - {{days[index] | leftpad}}</p>
                </td>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>上午</td>
                <td v-for="(day, index) in days" @click="slideUp"><span :class="{green: act(index)}">可约</span></td>
              </tr>
              <tr>
                <td>下午</td>
                <td v-for="(day, index) in days" @click="slideUp"><span :class="{green: act(index+6)}">可约</span></td>
              </tr>
            </tbody>
          </table>
          </div>
        </swiper-slide>
          <div class="swiper-pagination"  slot="pagination"></div>
      </swiper>

    </div>

  </div>

</section>
</template>

<script>
export default {
  name: 'HouseCall',
  computed: {
    nowTime: function(){
      return new Date().getTime()
    },
    day: function(){
      return new Date().getDay()
    },
    mon: function(){
      var monT = this.nowTime - (this.day-1)*this.oneDay
      return new Date(monT).getDate()
    },
    days: function(){
      var weeks = [this.mon, this.mon+1,this.mon+2,this.mon+3,this.mon+4,this.mon+5,this.mon+6]
      return weeks
    },
    month: function(){
      return new Date().getMonth() + 1
    },
  },
  methods: {
    act: function(index){
      if (index%2>0) {
        return true
      }
      return false
    },
    slideUp(e){
      if (e.target.classList.contains('green')) {
        this.$store.dispatch('login');
      }
    }
  },
  data() {
      return {
        oneDay: 24*60*60*1000,
        weeks: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'],
        swiperOption: {
          notNextTick: false,
          autoplay: 0,
          direction : 'horizontal',
          grabCursor : true,
          // setWrapperSize :true,
          autoHeight: true,
          pagination : '.swiper-pagination',
          paginationClickable :true,
          mousewheelControl : false,
          observeParents:true,
          keyboardControlL: true,
          // 观察模式，监测变动后重新初始化
          observer:true,
          debugger: true,
        }
      }
    },

}
</script>

<style lang="css" scoped>
  .house-call{
    display: none;
    overflow: hidden;
  }
  .call-container{
    width: 100%;
    padding: 10px 10px 30px 10px;
    background-color: #fff;
  }
  .call-container figcaption{
    text-align: center;
    padding: 5px;
    font-size: 1.2em;
  }
  .container-box{
    padding-left: 7px;
  }
  .call-container .container-box span{
    line-height: 30px;
    font-size: 1.2em;
    position: relative;
    display: inline-block;
  }
  .call-container .container-box-fee{
    float: right;
  }
  .call-container .container-box-title::before,
  .call-container .container-box-fee::before{
    content: '';
    width: 3px;
    height: 12px;
    background-color: rgba(79, 187, 83, 0.8);
    position: absolute;
    left: -7px;
    top: 9px;
  }

  .container-cell{
    width: 100%;
    margin-top: 10px;
    border-radius: 5px;
  }
  .cell-item{
    padding-bottom: 30px;
  }
  .cell-item table{
    width: 100%;
    border-spacing: 0;
    border-collapse: collapse;
    border-radius: 5px;
  }
  .cell-item table td{
    padding: 2px;
    border: 1px solid #ccc;
    text-align: center;
    line-height: normal;
  }
  .cell-item table thead td p{
    color: #333;
    font-size: 10px;
    font-weight: 300;
  }
  .cell-item table thead td p:nth-of-type(2){
    line-height: 1.5em;
  }
  .cell-item table td:first-child{
    padding: 10px 2px;
  }
  td > img{
    width: 18px;
    height: 18px;
    margin: 0 auto;
  }
  .cell-item table tbody tr td{
    color: #fff;
  }
  .cell-item table tbody tr td:first-child{
    color: #333;
  }
  td > .green{
    background: rgb(43, 161, 245);
    border-radius: 50px;
    border: 3px solid rgb(43, 161, 245);
    padding: 8px 3px;
    vertical-align: bottom;
    cursor: pointer;
  }
</style>



