<template lang="html">
  <aside class="menu">
    <span class="menu-trigger" @click="slide">ᠹᠳ ᠹᠳᠺᠵ ᠯᠺᠵᠹᠳ</span>
    <ul class="menu-group" :class="{jump_in: op}">
      <a href="person-account.html"><li class="menu-item">个人中心</li></a>
      <a href="#"><li class="menu-item">中文版官网</li></a>
    </ul>
  </aside>
</template>

<script>
export default {
  name: 'Aside',
  data(){
    return {
      op: false
    }
  },
  methods: {
    slide: function(){
        this.op = !this.op
    }
  }
}
</script>

<style lang="css" scoped>
  aside{
    position: fixed;
    bottom: -60px;
    right: 6px;
    transform: rotate(90deg);
    transform-origin: right top;
  }
  .menu-trigger{
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    border: 1px solid #bfcbd9;
    color: #1f2d3d;
    -webkit-appearance: none;
    text-align: center;
    box-sizing: border-box;
    outline: none;
    margin: 0;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    padding: 10px 15px;
    font-size: 14px;
    border-radius: 4px;
    background: #eefff5 url(../assets/imgs/menu.svg) no-repeat 7% center;
    background-size: 26px 26px;
    padding-left: 40px;
  }
  .menu-group{
    margin-top: 10px;
    opacity: 0;
  }
  .menu-item{
    display: block;
    margin-top: 5px;
    background-color: red;
    border-radius: 15px;
    padding: 5px 10px;
  }
  .jump_in{
    opacity: 1;
  }
</style>



