<template lang="html">
  <div class="quick-search">
    <section class="search-label">
      <div class="search-container">
        <div class="search-wrapper">
          <section class="search-header">
            <span class="header-logo" :style="{backgroundImage: 'url(static/imgs/icon.png)'}"></span>
            <p class="header-title">快速预约</p>
            <p class="header-description">内蒙古 9 大医院、 5 大体检中心， 一分钟预约</p>
          </section>
          <section class="search-body">
            <ul class="search-list">
              <a v-for="(item, index) in groups" @click="toggle(index)">
                <li class="search-item">
                  <div class="item-img"></div>
                  <p>{{item}}</p>
                </li>
              </a>
              <a href="tel: 0471-2243257" class="search-help"><li class="search-item"><div class="item-img"></div><p>人工客服</p></li></a>
            </ul>
          </section>
        </div>
      </div>
    </section>
    <section class="hospital-group">
      <toggle-button></toggle-button>
      <hospitals :active="blue" :hospitals="hospitals"></hospitals>
      <hot-departments></hot-departments>
      <healths></healths>
    </section>
  </div>
</template>

<script>
import ToggleButton from '../components/ToggleButton'
import Hospitals from '../components/Hospitals'
import HotDepartments from '../components/HotDepartments'
import Healths from '../components/Healths'

export default {
  name: 'quick-search',
  components: {
    ToggleButton,
    Hospitals,
    HotDepartments,
    Healths
  },
  data(){
    return {
      i: 'null',
      blue: true,
      groups: ['找医院', '找科室', '体检服务'],
      hospitals: [
        {title:'内蒙古自治区肿瘤医院',flag: '公立医院', phone: '0471-2243257', location: '内蒙古呼和浩特市昭乌达路 42 号', img: 'url(static/imgs/nmgzl.jpg)'},
        {title:'内蒙古医科大学附属医院', flag: '公立医院', phone: '0471-3451191', location: '呼和浩特市新华大街 1 号', img: 'url(static/imgs/nmgfy.jpg)'},
        {title:'内蒙古人民医院', flag: '公立医院', phone: '0471-6620000', location: '呼和浩特市昭乌达路 20 号', img: 'url(static/imgs/nmgmy.jpg)'},
        {title:'内蒙古国际蒙医医院', flag: '公立医院', phone: '0471-5182020', location: '呼和浩特市大学东街 83 号', img: 'url(static/imgs/nmgmy.jpg)'},
        {title:'内蒙古第二附属医院', flag: '公立医院', phone: '0471-6351329', location: '呼和浩特市回民区文化宫街营坊道 1 号', img: 'url(static/imgs/nmgdefs.jpg)'},
        {title:'内蒙古第一医院', flag: '公立医院', phone: '0471-5281222', location: '呼和浩特市玉泉区南二环路 150 号', img: 'url(static/imgs/nmgdy.jpg)'},
        {title:'内蒙古仁济医院', flag: '私立医院', phone: '0471-3361120', location: '呼和浩特市呼伦贝尔南路 103 号(东瓦窑十字路口南50米路西)', img: 'url(static/imgs/nmgrj.jpg)'},
        {title:'内蒙古第三医院', flag: '公立医院', phone: '0471-3958209', location: '呼和浩特市回民区新华西街与呼钢东路交汇西 600 米路北', img: 'url(static/imgs/nmgds.jpg)'},
        {title:'中国人民解放军第二五三医院', flag: '公立医院', phone: '0471-6514755', location: '呼和浩特市爱民街 111 号', img: 'url(static/imgs/nmg253.jpg)'}
      ]
    }
  },
  methods: {
    toggle: function(index){
      this.i = index
      var hospital_group = $(this.$el).find('.hospital-group')
      var toggle_button = $(this.$el).find('.toggle-button')
      var hospital_title = $(this.$el).find('.title')

      var hospitals = $(this.$el).find('section.hospital')[0]
      var hot_departments = $(this.$el).find('div.hot-group-container')[0]
      var healths = $(this.$el).find('div.health-container')[0]
      var groups = [hospitals, hot_departments, healths]

      $(toggle_button).removeClass('close')
      $(toggle_button).addClass('open')
      $(hospital_group).slideDown('500')
      Array.from(hospital_title).forEach(i => {
        i.style.display = 'none'
      });
      groups.forEach(function(i){
        i.style.display = 'none'
      })
      groups[this.i].style.display = 'block'
    }
  }
}
</script>

<style lang="css" scoped>
  .hospital-group{
    width: 100%;
    background: #fff;
    position: relative;
    display: none;
    overflow: hidden;
  }
  .search-label{
    width: 100%;
    min-height: 90px;
    background-color: #fff;
  }
  .search-container{
    width: 94%;
    margin: 0 auto;
    border: 1px solid #fff;
    border-radius: 6px;
    padding-top: 12px;
  }
  .search-wrapper{
    background-color: #FFF;
    width: 100%;
    min-height: 75px;
    padding: 12px 5px 8px 10px;
    position: relative;
    border-radius: 5px 5px 0 0;
    border: 1px solid #e3e3e3;
    box-shadow: 2px 2px 5px #e3e3e3;
    border-bottom: 2px solid #4caf50;
  }

  .search-header{
    border-bottom: 1px solid #d6d6d6;
    padding-bottom: 10px;
  }
  .header-logo{
    display: inline-block;
    float: left;
    margin: 0 12px 0 3px;
    width: 45px;
    height: 45px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  .header-title{
    font-size: 1.4em;
    line-height: 1.4em;
  }
  .header-description{
    font-size: 1.2em;
    line-height: 1.6em;
    color: #707070;
    padding-left: 60px;
  }

  .search-body{
    padding: 5px 10px 5px 12px;
  }
  .search-list{
    width: 100%;
  }
  .search-list::after{
    content: '';
    display: block;
    clear: both;
  }
  /*.search-list a{
    display: inline-block;
    width: 25%;
    float: left;
  }*/
  .search-item{
    display: inline-block;
    width: 25%;
    float: left;
    height: auto;
  }
  .search-item p{
    text-align: center;
    margin-top: 5px;
    color: #707070;
  }
  .search-item>.item-img{
    height: 45px;
    margin: 6px 0;
  }
  .search-list a:nth-of-type(1)>.search-item>.item-img{
    background: url(../assets/imgs/hospital.svg) no-repeat 50% 100%;
    background-size: auto 100%;
  }
  .search-list a:nth-of-type(2)>.search-item>.item-img{
    background: url(../assets/imgs/group.svg) no-repeat 50% 50%;
    background-size: auto 86%;
  }
  .search-list a:nth-of-type(3)>.search-item>.item-img{
    background: url(../assets/imgs/health.svg) no-repeat 50% 100%;
    background-size: auto 90%;
  }
  .search-list a:nth-of-type(4)>.search-item>.item-img{
    background: url(../assets/imgs/server.svg) no-repeat 50% 100%;
    background-size: auto 100%;
  }
</style>



