<template lang="html">
  <div class="health-projects">
  <section class="health-project">
    <div class="project-container">
      <ul class="project-list">
        <li class="project-item" v-for="(project, index) in projects" @click="slide_toggle(index)" :class="{'project-item-act': i==index }">
          <i class="item-icon" :style="{backgroundImage: projects[index].img}"></i>
          <p class="item-title">{{project.title}}</p>
        </li>
      </ul>
    </div>
  </section>
  <project-detail></project-detail>
  </div>
</template>

<script>
import ProjectDetail from './ProjectDetail'
import api from '../api/health'

export default {
  name: 'HealthProjects',
  data(){
    return {
      i: 'null',
      projects: api.projects
    }
  },
  methods: {
    slide_toggle: function($index){
      this.i = $index
      var projects = $(this.$el).find('.project-item')
      var project = $(this.$el).find('.project-item')[this.i]
      var pp = (this.projects)[this.i].title
      console.log(pp)
      var projectDetails = $(this.$el).find('.project-detail')
      projectDetails.slideDown('500')
    }
  },
  components: {
    ProjectDetail
  }
}
</script>

<style lang="css">
  .health-projects{
    width: 100%;
    display: none;
    overflow: hidden;
  }
  .project-container{
    width: 100%;
    margin: 0 auto;
  }
  .project-list{
    width: 100%;
    white-space: nowrap;
    overflow-x: auto;
    padding-bottom: 5px;
    /*background: linear-gradient(180deg, #7ab9e4, #67a8e4);*/
    background: linear-gradient(180deg, #2196F3, #03A9F4);
  }
  .project-item{
    width: 90px;
    height: 90px;
    display: inline-block;
    vertical-align: text-bottom;
    border-radius: 15px;
    margin: 5px;
    margin-left: 0;
    transition: all 0.3s linear;
  }
  .project-item-act{
    background-color: #5b68c1;
  }
  .project-item .item-icon{
    display: block;
    margin: 10px auto;
    width: 36%;
    padding-bottom: 36%;
  }
  .project-item .item-title{
    text-align: center;
    margin-top: 5px;
    color: #fff;
    white-space: pre-line;
  }
  .item-icon{
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
</style>



