<template lang="html">
  <section class="title" :style="{display: dp}">
    <p>{{title}}</p>
  </section>
</template>

<script>
export default {
  name: 'title',
  data (){
    return {

    }
  },
  props: ['title', 'dp']
}
</script>

<style lang="css">
  .title{
    width: 100%;
    /*border-bottom: 1px solid #e8e8e8;*/
    margin: 25px 0 15px;
  }
  .title p{
    padding: 6px 0;
    text-align: center;
    color: #707070;
    font-size: 1.3em;
  }
</style>



