<template lang="html">
  <div class="doctors-container">
  <div class="doctors" :style="{display: dp}" v-if="!doctors">
    <section class="doctor">
      <ul class="doctor-list">
        <template v-for="(person, index) in people">
          <li class="doctor-item"  @click= "slide_toggle(index)">
            <div class="item-img" :style="{backgroundImage: people[index].img}"></div>
            <p class="item-title">{{person.title}}</p>
            <span class="item-location">{{person.location}}</span><span class="item-group">{{person.group}}</span>
            <p class="item-act">擅长：{{person.act}}</p>
          </li>
          <house-call></house-call>
        </template>
      </ul>
      <p class="loadmore">加载更多医生</p>
    </section>
  </div>
  </div>
</template>

<script>
import Spinner from './Spinner'
import HouseCall from './HouseCall'
import {mapState} from 'vuex'
import api from '../api/hospital'

export default {
  name: 'doctors',
  methods: {
    slide_toggle: function(index) {
      this.i = index
      var li = $(this.$el).find('.doctor-item')
      var lii = $(this.$el).find('.doctor-item')[this.i]

      // $(li).next('.house-call').slideUp('300')

      $(lii).find('.item-act').toggleClass('active')
      $(lii).next('.house-call').slideToggle('300')
    },
  },
  computed: {
    ...mapState({
      doctors: state => state.toggle.doctors
    })
  },
  data() {
    return {
      i: '-1',
      title: '热门医生',
      people: api.doctors
    }
  },
  components: {
    Spinner,
    HouseCall
  },
  props: ['dp']
}
</script>

<style lang="css" scoped>
  .doctors-container{
    background-color: white;
  }
  .doctors{
    /*display: none;
    overflow: hidden;*/
  }
  .active{
    white-space: normal!important;
    height: auto;
    /*padding-bottom: 10px;
    border-bottom: 1px solid #9c9c9c;*/
  }
  .doctor{
    width: 100%;
    background: #fff;
  }
  .doctor-list{
    margin: 9px 0 5px 0px;
    width: 100%;
  }
  .doctor-list li{
    display: block;
    background-color: #FFF;
    width: 100%;
    min-height: 75px;
    border-bottom: 2px solid rgba(79, 187, 83, 0.8) ;
    position: relative;
    cursor: pointer;
  }
  .doctor-item{
    color: #9c9c9c;
    padding: 9px 0 5px 10px;
  }
  .doctor-item>.item-img{
    border-radius: 50%;
    width: 46px;
    height: 46px;
    float: left;
  }
  .item-img{
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-size: cover;
  }
  .doctor-item>.item-title{
    margin-left: 60px;
    font-size: 1.1em;
    line-height: 20px;
    color: #333;
  }
  .doctor-item>.item-location {
    margin-left: 14px;
    margin-top: 7px;
    line-height: 20px;
    display: inline-block;
  }
  .doctor-item>.item-group{
    position: relative;
    left: 20px;
  }
  .doctor-item>.item-group::before{
    content: '|';
    position: absolute;
    left: -10px;
    top: -1px;
  }
  .doctor-item>.item-act{
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    padding-left: 10px;
    padding-right: 10px;
    line-height: 20px;
    margin-top: 5px;
  }
  .loadmore{
    margin-top: 10px;
    padding: 6px 0;
    text-align: center;
    color: #0070c9;
  }
</style>



