<template lang="html">
  <div class="switch" :style="{backgroundImage: 'url(static/imgs/background.jpg)'}">
  <div class="switch-logo" :style="{backgroundImage: 'url(static/imgs/logo1.png)'}"></div>
  <div class="switch-msg">
    <ul>
      <router-link tag="li" to="/">
        <a></a>
      </router-link>
      <router-link tag="li" to="/m">
        <a></a>
      </router-link>
    </ul>
  </div>
</div>
</template>

<script>
export default {
  name: 'switch'
}
</script>

<style lang="css" scoped>
  .switch{
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  .switch-logo{
    width: 160px;
    height: 180px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 55px;
  }
  .switch-msg{
    width: 120px;
    height: 120px;
    position: absolute;
    left: 50%;
    top: 67%;
    transform: translateX(-50%) translateY(-50%);
    /*background-color: red;*/
  }
  ul li{
    list-style-type: none;
    width: 30%;
    height: 100%;
    float: left;
    border-radius: 30px;
    border: 1px solid #a1cb27;
    transition: all 160ms ease-out;
  }
  ul{
    width: 100%;
    height: 100%;
  }
  li:nth-child(1) a{
    background: url(../assets/imgs/zh_msg.png) no-repeat 50% 50%;
    background-size: 80% 55%;
  }
  li:nth-child(2){
    float: right;
  }
  li:nth-child(2) a{
    background: url(../assets/imgs/m_msg2.png) no-repeat 50% 50%;
    /*background-size: 80% 100%;*/
    background-size: 50% 70%;
  }
  a{
    width: 100%;
    height: 80%;
    display: block;
    top: 50%;
    transform: translateY(14%);
  }
</style>



