<template lang="html">
  <div class="group-toggle">
      <button class="toggle-button" @click="slideUp">
        <span class="icon-plus"></span>
        <span class="icon-plus"></span>
      </button>
  </div>
</template>

<script>
export default {
  name: 'ToggleButton',
  methods: {
    slideUp: function(){
      var hospital_group = $(this.$el).parent('.hospital-group')
      var toggle_button = $(hospital_group).find('.toggle-button')
      hospital_group.slideUp('500')
      $(toggle_button).removeClass('open')
      $(toggle_button).addClass('close')

      this.$emit('slideUp')
    }
  }
}
</script>

<style lang="css" scoped>
  .group-toggle{
    width: 100%;
    height: 55px;
    display: inline-block;
    background-color: #fff;
    position: relative;
  }
  .icon-plus{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: red;
    display: inline-block;
    background: url(../assets/group_imgs/line.png) no-repeat center center;
    background-size: 100%;
  }
  .icon-plus:nth-of-type(1){
    transform: rotate(45deg);
  }
  .icon-plus:nth-of-type(2){
    transform: rotate(-45deg);
  }
  .toggle-button{
    border: 0;
    outline: 0;
    display: block;
    position: relative;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    background-color: rgba(100  , 100, 100, 0.8);
    margin: 10px auto;
  }
  .close{
    animation: close 0.5s linear alternate;
  }
  .open{
    animation: open 0.3s ease-out alternate;
  }
  @keyframes close {
    0%{
      opacity: 1;
      transform: rotate(0) scale(1);
    }
    100%{
      opacity: 0.2;
      transform: rotate(-45deg) scale(0.6);
    }
  }
  @keyframes open {
    100%{
      opacity: 1;
      transform: rotate(0) scale(1);
    }
    0%{
      opacity: 0.2;
      transform: rotate(-45deg) scale(0.6);
    }
  }
</style>



