<template lang="html">
  <footer>
  <div class="copyright">
      <p v-if="!m">©2017 - 内蒙古蒙荣文化发展有限公司</p>
      <p v-else class="p_m" :style="{backgroundImage: bg}"></p>
  </div>
</footer>
</template>

<script>
export default {
  name: 'Footer',
  data(){
    return {
      bg: 'url(static/m_imgs/mrwh591.png)'
    }
  },
  props: ['m']
}
</script>

<style lang="css" scoped>
  footer{
    margin: 10px 0;
  }
  .copyright{
    text-align: center;
  }
  .copyright .p_m {
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    height: 35px;
    margin: 0 auto;
  }
</style>



