const state = {
  isLogin: false,
  hos: false,
  departments: false,
  doctors: false,
  house: false,
};

const getters = {
  isLogin: state => state.isLogin,
  hos: state => state.hos,
  departments: state => state.departments,
  doctors: state => state.doctors,
  house: state => state.house,
};

const mutations = {
  login(state){
    state.isLogin = !state.isLogin
  },
  toggleHos(state){
    state.hospitals = !state.hospitals
  },
  toggleDep(state){
    state.departments = !state.departments
  },
  toggleDoc(state){
    state.doctors = !state.doctors
  },
  toggleHou(state){
    state.house = !state.house
  }
};

const actions = {
  login({commit}){
    commit('login')
  },
  toggleHos({commit}){
    commit('toggleHos')
  },
  toggleDep({commit}){
    commit('toggleDep')
  },
  toggleDoc({commit}){
    commit('toggleDoc')
  },
  toggleHou({commit}){
    commit('toggleHou')
  }
};

export default {
  state,
  getters,
  mutations,
  actions
}



