<template lang="html">
  <div class="hot-group-container">
  <ul class="group-list">
    <router-link :to="{ name: 'groups', params: {category: item.title}}" v-for="(item, index) in departments" :key="item.title">
      <li class="group-item">
        <i class="item-icon" :style="{backgroundImage: departments[index].img}"></i>
        <p class="item-title">{{item.title}}</p>
      </li>
    </router-link>
  </ul>
  <!-- <doctors></doctors> -->
</div>
</template>

<script>
import Doctors from './Doctors'
import api from '../api/hospital'

console.log(api);

export default {
  name: 'HotDepartments',
  data(){
    return {
      departments: api.hot_departments
    }
  },
  components: {
    Doctors
  }
}
</script>

<style lang="css" scoped>
.hot-group-container{
  width: 100%;
  margin: 0 auto;
  border-radius: 6px;
}
.hot-group-container .group-list{
  width: 100%;
  float: left;
  padding-bottom: 30px;
  background: linear-gradient(180deg, #fff, #7ab9e4, #67a8e4 70%, rgba(242, 248, 253, 0.1));
}
.hot-group-container .group-list a{
  display: inline-block;
  width: 25%;
  float: left;
  border-right: 1px solid rgba(255,255,255,0.22);
  margin-right: -1px;
  margin-left: 1px;
  border-bottom: 1px solid rgba(255,255,255,0.52);
}
.hot-group-container .group-item{
  width: 100%;
  height: auto;
  margin: 15px 0;
}
.hot-group-container .group-item .item-icon{
  display: block;
  margin: 0 auto 10px;
  width: 36%;
  padding-bottom: 36%;
  background-size: 100% 100%;
}
.hot-group-container .group-item .item-title{
  text-align: center;
  margin-top: 5px;
  color: #fff;
}
.item-icon{
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
</style>



