<template lang="html">
	        <div class="list-wrapper">
	        <div class="personal-list">
	        <section class="personal-card content-card">
	        	<div class="personal-msg-title"><span>基本资料</span></div>
	        	<div class="personal-msg-avatar">
	        		<ul class="msg-list msg-avatar-list">
	        			<li class="avatar avatar-title">头像</li>
	        			<li class="avatar avatar-imgs"><img src="static/imgs/ph.jpg" alt="avatar"></li>
	        		</ul>
	        	</div>

	        	<div>
	        		<ul class="msg-list" @click="slide(1)">
	        			<li class="account account-title">真实姓名</li>
	        			<li class="msg-arrow"></li>
	        			<li class="account account-imgs">草原医道</li>
	        		</ul>
              <p class="edit-msg"><input  type="text" name="id" placeholder="输入你的真实姓名" value="草原"><span class="save-msg">保存</span>
              </p>
	       	   </div>

	        	<div>
	        		<ul class="msg-list" @click="slide(2)">
	        			<li class="tel tel-title">手机号码</li>
	        			<li class="msg-arrow"></li>
	        			<li class="tel tel-imgs">18814568956</li>
	        		</ul>
              <p class="edit-msg"><input  type="text" name="id" placeholder="输入你的手机号码" value="18814568956"><span class="save-msg">保存</span>
              </p>
	        	</div>
	        	<div class="personal-msg-id">
	        		<ul class="msg-list" @click="slide(3)">
	        			<li>身份证号码</li>
	        			<li class="msg-arrow"></li>
	        			<li>159861595215255</li>
	        		</ul>
              <p class="edit-msg"><input  type="text" name="id" placeholder="输入你的身份证" value="159861595215255"><span class="save-msg">保存</span>
              </p>
	        	</div>
	        </section>

</div>
  <order></order>
</div>
</template>

<script>
import Order from './Order'

export default {
  name: 'Personal',
  data(){
    return {
        i: '-1'
    }
  },
  methods: {
    slide: function(index){
      var msg_list = $('.msg-list')[index]
      var msg = $(msg_list).next('p')
      msg.slideToggle('500')
    }
  },
  components: {
    Order
  }
}
</script>

<style lang="css" scoped>
  a{
    /*color: #000;*/
  }
  .list-title p{
    padding: 10px 30px;
    height: 100%;
    width: 100%;
    background: #2196f3;
    color: #fff;
    text-align: center;
  }
  .content-card{
    height: auto;
    padding-top: 0;
    cursor: initial;
  }
  .content-card div:hover .msg-arrow{
    left: 15px;
  }
  .content-card .msg-list:hover{
    cursor: pointer;
  }
  .personal-order .content-card{
    margin-top: 20px;
  }
  .personal-card div ul li{
    list-style-type: none;
  }
  .personal-card div{
    /*height: 45px;*/
    width:  100%;
    line-height: 45px;
  }
  .personal-health{
    margin-top: 20px;
  }
  div{
    box-sizing: border-box;
  }

  .personal-msg-title,
  .personal-order-title{
    background-color: #2196F3;
  }
  .personal-msg-title span{
    padding-left: 25px;
    color: #fff;
    font-size: 1.2em;
  }
  .msg-list,
  .order-time-list{
    padding-left: 25px;
    padding-right: 25px;
    border-bottom:  1px solid #e2e2e2;
    height: 100%;
  }
  .msg-health-list,
  .order-time-list{
    border: 0;
  }
  .msg-list li{
    display: inline-block;
  }
  .personal-order .personal-card div .msg-list{
    cursor: pointer;
  }
  .personal-order .personal-card div .msg-list:nth-of-type(4){
    cursor: initial;
  }
  .msg-arrow{
    float: right;
    height: 45px;
    width: 20px;
    position: relative;
    left: 5px;
    background:  url('../assets/imgs/arrow.png') no-repeat 50% 50%;
    background-size: 18px 25px;
    transition: all 160ms ease-out;
  }
  .msg-list li:nth-of-type(3),
  .msg-list li:nth-of-type(2),
  .order-time{
    float: right;
  }
  .personal-card .personal-msg-avatar{
    height: 70px;
    line-height: 70px;
  }
  .avatar-imgs{
    width: 60px;
    height: 60px;
    position: relative;
    transform: translateY(5px);
  }
  .avatar-imgs img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
  p.edit-msg{
    display: none;
    overflow: hidden;
    width: 90%;
    height: 40px;
    margin: 20px auto;
    margin-top: 5px;
    padding-left: 10px;
    border-bottom: 2px solid #2196f3;
  }
  span.save-msg{
    background-color: #2196f3;
    color: #fff;
    padding: 3px 6px;
    border-radius: 5px;
    font-size: 1.3em;
    width: 20%;
    height: 100%;
    cursor: pointer;
  }
  .edit-msg input {
    width: 80%;
    outline: 0;
    border: 0;
  }
</style>



