<template lang="html">
  <div class="index">
    <search-box></search-box>
    <gallery :top="'-50px'"></gallery>
    <quick-search></quick-search>
    <title-component :dp="dp" :title="doctortitle"></title-component>
    <doctors :dp="dp"></doctors>
    <title-component :dp="dp" :title="hospitaltitle"></title-component>
    <hospitals :active="blue" ></hospitals>
    <footer-component></footer-component>
    <login></login>
  </div>
</template>

<script>
  import  SearchBox from '../components/SearchBox'
  import  TitleComponent from '../components/Title'
  import  Gallery from '../components/Gallery'
  import  QuickSearch from '../views/quick-search'
  import  Hospitals from '../components/Hospitals'
  import  Doctors from '../components/Doctors'
  import  FooterComponent from '../components/Footer'
  import  Login from '../components/Login'



  export default {
    name: 'index',
    data(){
      return {
        dp: 'block',
        doctortitle: '热门医生',
        hospitaltitle: '热门医院',
        blue: false,
        m: false
      }
    },
    components: {
      SearchBox,
      TitleComponent,
      Gallery,
      QuickSearch,
      Hospitals,
      Doctors,
      FooterComponent,
      Login
    }
  }
</script>

<style lang="css">

</style>



