<template lang="html">
  <div class="hospital-departments">
  <section class="hospital-department">
    <div class="department-container">
       <ul class="department-list" >
         <li class="department-item" v-for="(item, index) in projects"  @click="slide_toggle(index)" :class="{active: i==index }">
           <i class="item-icon" :style="{backgroundImage: projects[index].img}"></i>
           <p class="item-title">{{item.title}}</p>
         </li>
       </ul>
    </div>
  </section>
  <doctors :dp="display"></doctors>
  </div>
</template>

<script>
import Doctors from './Doctors'
import api from '../api/hospital'

export default {
  name: 'HospitalDepartments',
  data(){
    return {
      display: 'none',
      i: '-1',
      projects: api.hot_departments
    }
  },
  methods: {
    slide_toggle: function($index){
      this.i = $index
      var items = $(this.$el).find('.department-item')[this.i]
      var pp = (this.projects)[this.i]
      console.log(pp);
      var doctors = $(this.$el).find('.doctors')
      doctors.slideDown('500')
    }
  },
  components: {
    Doctors
  }
}
</script>

<style lang="css" scoped>
  .hospital-departments{
    width: 100%;
    display: none;
    overflow: hidden;
  }
  .department-container{
    width: 100%;
    margin: 0 auto;
    border-radius: 6px;
  }
  .department-list{
    width: 100%;
    white-space: nowrap;
    overflow-x: auto;
    padding-bottom: 15px;
    /*background: linear-gradient(180deg, #7ab9e4, #67a8e4 80%, rgba(242, 248, 253, 0.1));*/
    background: linear-gradient(180deg, #2196F3, #03A9F4);
  }
  .department-item{
    width: 80px;
    height: 80px;
    display: inline-block;
    /*border: 1px solid rgba(255,255,255,0.5);*/
    border-radius: 15px;
    margin: 5px;
    margin-left: 0;
    transition: all 0.3s linear;
  }
  .active{
      background-color: #0e1435;
  }
  .department-item .item-icon{
    display: block;
    margin: 10px auto;
    width: 45%;
    padding-bottom: 45%;
    /*background-size: 100% 100%;*/
  }
  .department-item .item-title{
    text-align: center;
    margin-top: 5px;
    color: #fff;
  }
  .department-list>.department-item .item-icon{
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
</style>



