import Vue from 'vue'
import Router from 'vue-router'
import Switch from '@/views/switch'
import Index from '@/views/index'
import IndexM from '@/views/index_m'
import Login from '@/components/Login'
import User from '@/components/Personal'
import Doctors from '@/components/Doctors'

import Spinner from '@/components/Spinner'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/ss',
      component: Spinner
    },
    {
      path: '/s',
      component: Switch
    },
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/groups/:category',
      name: 'groups',
      component: Doctors
    },
    {
      path: '/m',
      component: IndexM
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/user/:id',
      name: 'user',
      component: User
    }
  ]
})



