<template lang="html">
  <header>
  <div class="header-search">
    <span class="address">内蒙古</span>
    <input type="search" placeholder="搜索医院，科室，疾病">
    <span class="btn">搜索</span>
  </div>
</header>
</template>

<script>
export default {
  name: 'SearchBox'
}
</script>

<style lang="css">
header{
  height: 44px;
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
  background-color: rgba(190, 190, 190, 0.2);
  z-index: 10;
}
.header-search{
  height: 100%;
  width: 100%;
  position: relative;
  padding-left: 20px;
}
.header-search .address{
  position: absolute;
  left: 2%;
  top: 16px;
  color: #fff;
  width: 54px;
  background: url(../assets/imgs/arrowd.png)  no-repeat 100% 50%;
  background-size: auto 16px;
}
.btn{
  position: absolute;
  right: 2%;
  top: 9px;
  padding: 5px 12px;
  border: 1px solid #dadada;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}
.header-search input{
  width: 68%;
  height: 30px;
  margin: 8px 12% 1px;
  padding-left: 34px;
  background: url(../assets/imgs/search-more.png) no-repeat 5px 50%;
  background-size: 16px 16px;
  background-color: rgba(56, 105, 86, 0.1);
  outline: 0;
  border: 0;
  border-radius: 5px;
  color: #fff;
  font-size: 1.3em;
  box-sizing: border-box;
}
.header-search input:focus {
  border:#35a5e5 0px solid;
  box-shadow: 0 0 8px rgba(43, 208, 255, 1);
  -webkit-box-shadow: 0 0 8px rgba(43, 208, 255, 1);
  -moz-box-shadow: 0 0 8px rgba(43, 208, 255, 1);
}
</style>



