<template lang="html">
  <div class="login" :class="{slideUp: active}">
    <div class="overlay"></div>
    <div class="inner">
    <toggle-button v-on:slideUp="toggle"></toggle-button>
    <div class="title">
      <!-- <p>快速登陆</p> -->
      <p>为了挂号预约和收到预约成功的信息</p>
      <p>您必须填写以下信息</p>
    </div>
      <div class="cell">
        <div class="phone label">
          <label for="phone" style="margin-left: 2px"><img src="static/imgs/name.svg" alt=""></label>
          <input type="text" style="padding-left: 6px" name="name" value="" maxlength="11" placeholder="输入你的姓名">
        </div>
        <div class="phone label">
          <label for="phone"><img src="static/imgs/id.svg" alt=""></label>
          <input type="phone" name="id" value="" maxlength="18" placeholder="输入你的身份证以便挂号">
        </div>
        <div class="phone label">
          <label for="phone"><img src="static/imgs/phone.svg" alt=""></label>
          <input type="phone" name="phone" value="" maxlength="11" placeholder="输入你的手机号">
        </div>
        <div class="code label">
          <label for="code"><img src="static/imgs/code.svg" alt=""></label>
          <input type="phone" name="code" value="" maxlength="4" placeholder="输入你收到的验证码">
          <span class="get-code">获取验证码</span>
        </div>
      </div>
    <div class="submit">
      <input type="submit" name="" value="确定" @click="register">
    </div>
  </div>
</div>
</template>

<script>
import ToggleButton from './ToggleButton'
import {mapGetters} from 'vuex'

export default {
  name: 'Login',
  computed: {
    ...mapGetters({
      active: 'isLogin'
    })
  },
  methods: {
    toggle(){
       var toggle_button = $(this.$el).find('.toggle-button')
       this.$store.dispatch('login')
       $(toggle_button).removeClass('open')
       $(toggle_button).addClass('close')
    },
    register(){
      this.$store.dispatch('login')
    },
    // active(){
    //   return false
    //   // this.$store.toggle.state.isLogin
    // }
  },
  components: {
    ToggleButton
  }
}
</script>

<style lang="css" scoped>
::-webkit-input-placeholder { /* WebKit browsers */
color: #999;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
color: #999;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
color: #999;
}
:-ms-input-placeholder { /* Internet Explorer 10+ */
color: #999;
}
.login{
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 100%;
  left: 0;
  z-index: 10;
  background-color: rgb(255, 255, 255);
  transition: all 0.5s ease;
}
.slideUp{
  top: 0;
}
.overlay{
  filter: blur(5px);
  width: 100%;
  height: 100%;
}
.inner{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.title{
  color: #3c3c3c;
}
.title p {
  color: #333;
}
.cell{
  width: 100%;
  padding: 5px 10px;
}
.label{
  width: 100%;
  display: flex;
}
label{
  width: 40px;
  vertical-align: middle;
  display: flex;
  align-items: center;
  text-align: center;
  border: 0;
}
label img{
  width: 24px;
  height: 24px;
  vertical-align: middle;
  display: inline-block;
}
input{
  flex: 1;
  padding: 8px;
  border: 0;
  outline: 0;
  background-color: inherit;
}
.label{
  border-bottom: 1px solid #eee;
}
.get-code{
  padding: 8px 5px;
  line-height: 24px;
  color: #2196f3;
  border: 1px solid rgba(213, 213, 213, 0.66);
}
.submit{
  width: 100%;
}
input[type="submit"]{
  width: 90%;
  padding: 12px;
  display: block;
  margin: 10px auto;
  background-color: #2196F3;
  color: #fff;
}
</style>



