<template lang="html">
  <div class="personal-order">
    <section class="personal-card" v-for="order in orders">
      <div class="personal-order-title" :style="{backgroundColor: hospitalBg}"><span>预约情况</span><span class="order-msg-time">{{order.time}}</span></div>
      <div class="personal-order-hospital">
        <ul class="msg-list order-hospital-list">
          <li>医院</li>
          <li>{{order.address}}</li>
        </ul>
      </div>

      <div class="personal-order-office">
        <ul class="msg-list order-office-list">
          <li>科室</li>
          <li>{{order.group}}</li>
        </ul>
      </div>

      <div class="personal-order-doctor">
        <ul class="msg-list order-doctor-list">
          <li>医生</li>
          <li>{{order.doctor}}</li>
        </ul>
      </div>
      <div class="personal-order-time">
        <ul class=" order-time-list">
          <li class="order-title">时间</li>
          <li class="order-time">{{order.time}}</li>
        </ul>
      </div>
    </section>

    <section class="personal-card" v-for="project in healths">
          <div class="personal-order-title" :style="{backgroundColor: healthBg}" ><span>体检详情</span><span class="order-msg-time">2017/03/05/ 9：30</span></div>
          <div class="personal-order-hospital">
            <ul class="msg-list order-hospital-list">
              <li>地点</li>
              <li>{{project.address}}</li>
            </ul>
          </div>
          <div class="personal-order-doctor">
            <ul class="msg-list order-doctor-list">
              <li>体检项目</li>
              <li>{{project.project}}</li>
            </ul>
          </div>
          <div class="personal-order-time">
            <ul class=" order-time-list">
              <li class="order-title">时间</li>
              <li class="order-time">{{project.time}}</li>
            </ul>
          </div>
        </section>

  </div>

</template>

<script>
export default {
  name: 'Order',
  data(){
    return {
      hospitalBg: 'rgba(79, 187, 83, 0.8)',
      healthBg: 'rgba(33, 150, 243, 0.79)',
      orders: [
        {time: '2017/03/05/ 9：30', address: '内蒙古医院', group: '外科', doctor: '李莉莉'},
        {time: '2017/03/05/ 9：30', address: '内蒙古医院', group: '外科', doctor: '李莉莉'}
      ],
      healths: [
        {time: '2017/03/05/ 9：30', address: '内蒙古肿瘤医院体检中心', project: '入职体检'}
      ]
    }
  }
}
</script>

<style lang="css" scoped>
  .personal-card{
    margin-top: 20px;
  }
  .personal-card div{
    height: 45px;
    width:  100%;
    line-height: 45px;
  }
  .personal-health{
    margin-top: 20px;
  }
  div{
    box-sizing: border-box;
  }

  .personal-order-title{
    background-color: #d6d6d6;
  }
  .personal-order-title>span{
    padding-left: 25px;
    font-size: 14px;
    color: #fff;
  }
  .msg-list,
  .order-time-list{
    padding-left: 25px;
    padding-right: 25px;
    border-bottom:  1px solid #e2e2e2;
    height: 100%;
  }
  .msg-list li,
  .order-time-list li{
    display: inline-block;
  }
  .personal-order .personal-card div .msg-list{
    cursor: pointer;
  }
  .personal-order .personal-card div .msg-list:nth-of-type(4){
    cursor: initial;
  }

  .msg-list li:nth-of-type(3),
  .msg-list li:nth-of-type(2),
  .order-time{
    float: right;
  }
  .personal-card .personal-msg-avatar{
    height: 70px;
    line-height: 70px;
  }
  .avatar-imgs{
    width: 60px;
    height: 60px;
    position: relative;
      transform: translateY(5px);
  }
  .avatar-imgs img{
    width: 100%;
    height: 100%;
  }
  .personal-msg-logout{
    text-align: center;
    margin-top: 30px;
  }
  .personal-msg-logout button{
    padding: 10px 20px;
    width: 80%;
    color: #fff;
    background: #000;
    border: 1px solid #666;
  }
  .order-msg-time{
    color: #008eff;
  }
</style>



